import auth0, {
    WebAuth
} from 'auth0-js';
import history from './history';
class Auth {
    auth0 = new WebAuth({
        domain: 'react-auth-project.auth0.com',
        clientID: 'ygL926gSZk9jyn6wF6gNwU8td42QyQ2V',
        redirectUri: 'http://localhost:1234/callback',
        responseType: 'token'
    });

    login = () => {
        this.auth0.authorize();
    }

    handelAuthentication = () => {
        this.auth0.parseHash((err,authRes)=>{
            if(authRes){
                localStorage.setItem('access_token',authRes.accessToken);
                localStorage.setItem('expires_at',JSON.stringify(authRes.expiresIn * 1000 + new Date().getTime()))
                this.loggedIn=true;
                history.replace('/');
            }else if(err){
                console.log('err',err);
            }
        });
    }

    logout = () => {
        ['access_token','expires_at'].forEach(item=>localStorage.removeItem(item));
        this.loggedIn=false;
        history.replace('/');
    }
    isAuthenticated =()=> {
      
        return  new Date().getTime() < +localStorage.getItem('expires_at');
    }

}
export default Auth